import React, { useState } from 'react';
import { useRecoilState } from 'recoil';
// import io from 'socket.io-client';
import { usernameState } from './../state';

// const socket = io();

// sockets test
// socket.on('hello', ({ message }) => alert(message));

function Dashboard() {
	const [roomCode, setRoomCode] = useState('');
	const [username, setUsername] = useRecoilState(usernameState);

	const onRoomCodeChange = (e) => {
		setRoomCode(e.target.value);
	};

	const onUsernameChange = (e) => {
		setUsername(e.target.value);
	};

	return (
		<div className="App">
			<header className="App-header">
				<h1>
					You Bet <span style={{ fontSize: '20px', color: 'rgba(0,0,0,0.5)' }}>(on)</span> Your Marbles
				</h1>
				<br />
				<input type="text" placeholder="Room code" onChange={onRoomCodeChange} value={roomCode} />
				<input type="text" placeholder="Username" onChange={onUsernameChange} value={username} />
				<button>Create Room</button>
				<button>Join Room</button>
			</header>
		</div>
	);
}

export default Dashboard;
