import React, { useState } from 'react';
import { useRecoilState } from 'recoil';
import randomWords from 'random-words';
import { usernameState } from './../state';
import { useHistory } from 'react-router-dom';
import axios from 'axios';

function Home() {
	const [roomCode, setRoomCode] = useState('');
	const [username, setUsername] = useRecoilState(usernameState);
	const [error, setError] = useState('');
	const history = useHistory();

	const onRoomCodeChange = (e) => {
		setRoomCode(e.target.value);
	};

	const onUsernameChange = (e) => {
		setUsername(e.target.value);
	};

	const createRoom = () => {
		const roomCode = randomWords(1);
		console.log(roomCode);
		const roomURL = `/${roomCode}/dashboard`;
		history.push(roomURL);
	};

	const joinRoom = () => {
		if (username === '') {
			setError('Please enter a username');
			return;
		}
		localStorage.setItem('username', username);
		const roomCode = randomWords(1);
		console.log(roomCode);
		const roomURL = `/${roomCode}`;
		history.push(roomURL);
		axios.get(`/${roomCode}/ping`).then((res) => {
			const roomExists = res.data.roomExists;
			if (roomExists) {
				history.push(`/${roomCode}`);
			} else {
				setError('Room does not exist, check or spelling or create a new room!');
			}
		});
	};

	return (
		<div className="App">
			<header className="App-header">
				<div style={{ width: 500 }}>
					<h1>Marble Bets</h1>
					<br />
					<button onClick={createRoom}>Create Room</button>
					<br />
					<h5>or</h5>
					<input
						type="text"
						placeholder="Enter a display name"
						onChange={onUsernameChange}
						value={username}
						style={{ width: 300 }}
					/>
					<br />
					<input
						type="text"
						placeholder="Room code"
						onChange={onRoomCodeChange}
						value={roomCode}
						style={{ width: 170 }}
					/>
					<button onClick={joinRoom}>Join Room</button>
					<p style={{ color: 'red' }}>{error}</p>
				</div>
			</header>
		</div>
	);
}

export default Home;
